@import url(https://fonts.googleapis.com/css2?family=Baloo+Thambi+2:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-image: linear-gradient(to right, #3a524f , #282c34);
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.container {
  width: 200px;
}

.board {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 100%;
}

button.cells:hover {
  background: #72b4af;
  transition-duration: .2s;
}

button.cells:focus, button.cells:hover {
  -webkit-animation: wobble 1s 1;
          animation: wobble 1s 1;
  transition-duration: .2s;
}

.cells {
  font-family: 'Baloo Thambi 2', cursive;
  font-size: 32px;
  -webkit-flex-basis: calc(100%/3);
          flex-basis: calc(100%/3);
  height: 50px;
  border: solid 1px;
  border-radius: .2rem;
  transition-duration: .2s;
}

@-webkit-keyframes wobble {
  25% {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }
  50% {
    -webkit-transform: rotate(-7deg);
            transform: rotate(-7deg);
  }
  75% {
    -webkit-transform: rotate(2deg);
            transform: rotate(2deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

@keyframes wobble {
  25% {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }
  50% {
    -webkit-transform: rotate(-7deg);
            transform: rotate(-7deg);
  }
  75% {
    -webkit-transform: rotate(2deg);
            transform: rotate(2deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

.champion-visible {
  -webkit-animation: championAnimate 1s;
          animation: championAnimate 1s;
  -webkit-animation-iteration-count:infinite;
          animation-iteration-count:infinite;
  transition-duration: .2s;
}

@-webkit-keyframes championAnimate {
  25% {
    color: deeppink;
  }
  75% {
    color: #e4f872;
  }
}

@keyframes championAnimate {
  25% {
    color: deeppink;
  }
  75% {
    color: #e4f872;
  }
}
